import './Replayer.less';

import { EventEmitter } from 'events';

import { adapterList } from './adapter/adapter.server';
import { EAppEvent } from './common/types/types.event.d';
import { IServerData } from './common/types/types.server';
import { ControlPanelComponent } from './component/ControlPanel/ControlPanel';
import { RendererComponent } from './component/Renderer/Renderer';
import { config } from './config';
import { ServiceData } from './service/service.data';
import { ServiceFramer } from './service/service.framer';
import { ServiceResource } from './service/service.resource';

export class Replayer extends EventEmitter {
  public static event = EAppEvent;

  public container: HTMLElement;
  public framer?: ServiceFramer;
  public resource?: ServiceResource;
  public renderer?: RendererComponent;
  public control?: ControlPanelComponent;

  constructor(container: string | HTMLElement, source?: string | IServerData) {
    super();
    const element = typeof container === 'string' ? (document.querySelector(container) as HTMLElement) : container;
    if (!element) throw new Error('Нет контэйнера для отрисовки');
    this.container = element;
    this.container.classList.add('ah-container');
    if (source) this.setData(source);
  }

  public async setData(source: string | IServerData) {
    const data = typeof source === 'string' ? await ServiceData.getData(source) : source;

    const adapter = adapterList[data.Version];
    if (!adapter) throw new Error(`Нет адаптера для версии "${data.Version}"`);

    this.framer = new ServiceFramer(adapter(data));
    this.resource = new ServiceResource(this.framer.skin);
    await this.resource.download();
    this.container.innerHTML = '';

    this.renderer = new RendererComponent(this.container, this.framer.map, this.resource.list, { autoresize: config.autoresize });
    this.control = new ControlPanelComponent(this.container, this.framer, this.renderer.render.bind(this.renderer), this.emit.bind(this));
    if (config.autoplay) this.control.play();
    else this.renderer.render(this.framer.state);
  }
}
